.about {
    text-align: center;
}

.about h1 {
    color: rgb(251, 227, 117);
    font-family: Lora, serif;
    font-size: 48px;
    font-weight: 600;
}

.about p {
    font-family: "Poppins";
    font-size: 18px;
    color: white;
    
}

@media screen and (max-width: 576px) {
    .about {
        text-align: left;
    }

    .about h1 {
        font-size: 30px;
    }
    .about p{
        text-align: justify;
        font-size: 14px;
    }
}

@media screen and (min-width: 576px) and (max-width: 768px) {
    .about {
        text-align: left;
    }

    .about h1 {
        font-size: 48px;
    }
    .about p{
        width: 90%;
        margin: auto;
        text-align: justify;
    }
}