.header h1{
    
    font-family: lora,serif;
    font-size: 86px;
    font-weight: 800;
    line-height: 110px;
    color: rgb(251, 227, 117);
}
.header button{
    border: none;
    background: rgb(236, 177, 10);
    color: black;
    border-radius: 10px;
    padding: 16px 40px;
    font-weight: 400;
    margin-top: 50px;
   
}
.header p{
    color: rgb(159, 234, 164);
    font-size: 26px;
}
.header{
    padding: 200px 0;
}

@media screen and (max-width: 576px) {
    .header h1{
        font-size: 60px;
        line-height: 80px;
    }
}

@media screen and (min-width: 576px) and (max-width: 768px) {
    .header h1{
        font-size: 70px;
        line-height: 80px;
    }
}