.roadmap h1{
    text-align: center;
    font-size: 48px;
    font-family: lora,serif;
    font-weight: 600;
    color: rgb(251, 227, 117);
}
.roadmap{
    position: relative;
}
.mapline{
    background-color: rgb(251, 227, 117);
    left: 50%;
    margin-left: -1%;
    width: 5px;
    content: " ";
    height: 95%;
    position: absolute;
    z-index: 1;
}
.mapbox{
    border: 2px solid grey;
    border-radius: 15px;
    color: white;
    font-weight: 300;
    padding: 20px 50px;
    justify-content: flex-start;
}
.mapbox p{
    font-family: poppins;
    line-height: 32px;
    font-size: 18px;
}
.mapbox h2{
    font-family: lora,serif;
    color: #FBE375;
    font-weight: 600;
}
.mapbox2{
    
    border: 2px solid grey;
    border-radius: 15px;
    color: white;
    font-weight: 300;
    padding: 20px 50px;
    
}
.mapbox2 p{
    font-family: poppins;
    line-height: 32px;
    font-size: 18px;
}
.mapbox2 h2{
    font-family: lora,serif;
    color: #FBE375;
    font-weight: 600;
}
.left{
    margin-right: auto;
    position: relative;
}
.right{
    margin-left: auto;
    position: relative;
}
.left::before{
    content: " ";
    height: 30px ;
    width: 30px;
    background: #FBE375;
    position: absolute;
    right: -99px;
    top: 40%;
    border: 6px solid #161B1A;
    z-index: 999;
    

    -webkit-transform: rotate(45deg);
    transform: rotate(45deg);
    border-radius: 5px;

}
.right::before{
    content: " ";
    height: 30px ;
    width: 30px;
    background-color: #FBE375;
    position: absolute;
    rotate: -45deg;
    left: -117px;
    top: 40%;
    border: 7px solid #161B1A;
    z-index: 999;
    border-radius: 5px;
}

@media screen and (max-width: 992px) {
    .mapbox,.mapbox2{
        padding: 15px;
    }
    .left::before{
        right: -86px;
    }
    .right::before{
        left: -100px;
    }
}
