.footer h1{
    color: rgb(251, 227, 117);
    font-size: 48px;
    font-family: lora,serif;
    font-weight: 600;
}
.icons ul {
    display: flex;
    justify-content: end;
}
.icons ul li {
    list-style-type: none;
}
.last{
    padding: 70px 0;
}
@media only screen and (max-width: 576px) {

    .footer h1{
        width: 100%;
        text-align: center;
    }
    .icons ul {
        width: 60%;
        margin: auto;
        justify-content: center;
        justify-content: space-evenly;
        padding: 30px 0;
    }
}