.communitytxt h1{
    font-size: 48px;
    font-weight: 600;
    font-family: lora,serif;
    color: #FBE375;
}
.communitytxt p{
    color: white;
    font-family: poppins;
    line-height: 32px;
    padding: 20px 0;
}
.communitytxt button{
    background: transparent;
    border-radius: 10px; 
    padding: 5px 25px;
    border-color: rgb(159, 234, 164);
    margin-right: 30px;
    font-size: 18px;
    font-weight: 600;
    color: rgb(159, 234, 164);
    line-height: 32px;
}
.commuunitypic{
    position: relative;
    
    
}
.image1 img{
    width: 60%;
    height: auto;
    left: 0;
    top: 0;
}
.image2 img{
    position: absolute;
    width: 60%;
    height: auto;
    left: 30%;
    top: 40%;
}
.image3 img{
    position: absolute;
    width: 60%;
    height: auto;
    left: 8%;
    top: 80%;
}
.community{
    padding-bottom: 500px;
    
}
.commbt{
    justify-content: center;
}

@media screen and (max-width: 576px){
    .communitytxt h1{
        font-size: 30px;
    }
    .communitytxt p{
        font-size: 16px;
    }
    .communitytxt button{
        padding: 3px 15px;
    }
    .community{
        padding-bottom: 0px;
        
        
    }
    .commuunitypic{
        margin-bottom: 300px;
    }
    .communitytxt{
        text-align: center;
        padding: 50px 0;
    }
}
@media screen and (min-width: 576px)and (max-width: 998px){
    .commuunitypic{
        margin-bottom: 400px;
        
    }
    .community{
        padding-bottom: 0px;
        
        
    }
    .communitytxt{
        text-align: center;
        padding: 50px 0;
    }
}