.navbar-brand p{
    color: white;
    font-weight: 700;
    font-size: 24px;
    margin-left: 10px;
    padding-top: 29px;
}
.navbar-brand img{
    height: 80px;
    width: 70px;
    transition: 1s;
}
.navbar-brand p{
    font-size: 24px;
    transition: 1s;
}
.fixed .navbar-brand p{
    font-size: 20px;
    
}
.fixed .navbar-brand img{
    height: 50px;
    width: 45px;
    margin-top: 13px;
}

.headbtn1{
    padding: 15px 40px;
    border: none;
    background-color: rgb(236, 177, 10);
    border-radius: 10px;
    line-height: 27px;
    text-align: center;
    font-weight: 500;
    font-size: 18px;
    transition: 1s;
}
.nav-item a{
    color:rgb(251, 227, 117);
    font-size: 18px;
    font-weight: 600;
    transition: 1s;
}


.navbar-toggler-icon{
    padding: 15px;
    
}

@media screen and (max-width: 1100px)  {
    .headbtn{
        display: none;
    }
}  


  .heade{
    transition: 0.8s;
    height: 120px;
    background-color: transparent;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    transition: 1s;
  }
  .heade.fixed{
    background-color: rgb(0, 0, 0,0.6);
    transition: 1s;
    height: 70px;
    z-index: 1000;
    border-bottom: 4px solid #ECB10A;
  }
  .fixed .headbtn1{
    padding: 8px 40px;
    transition: 0.8s;
  }
  .unique{
    font-size: 20px;
    
  }
  .fixed .unique{
    font-size: 16px;
    
  }

  
  .heade a {
    position: relative;
    text-decoration: none;
    /* color: white; */
    padding: 0 10px;
   
}
.heade a:hover {
    color: #ECB10A;
}

.heade a:after {
    content: "";
    position: absolute;
    background-color: #ECB10A;
    height: 3px;
    width: 0%;
    left: 0;
    bottom: -5px;
    transition: 0.5s;
    
}

.unique:hover:after {
    width: 100%;
    
}
#bg{
    background-color: black;
}


@media screen and (max-width: 990px)  {
.navbar-nav{
    background-color: rgb(0, 0, 0,0.9);
    border-bottom: 5px solid #ECB10A;
    text-align: center;
    
}
.nav-item a{
    font-size: 24px;
    padding: 10px 0;
}
.fixed .nav-item a{
    font-size: 20px;
}
}

.heade .icon{
    color: transparent;
}
