#frontcard{
    background: none;
    text-align: center;
    color: white;
    width: 202px;
    border: none;
    margin: auto;
    
    
}
#frontcard img ::before{
    background-image: url("../images/frame.png");
    background-size: 100%;
    content: "";
    position: absolute;
    z-index: 2;

}
.date{
    font-size: 12px;
    color: #939595;
}
.para{
    font-size: 14px;
}
.pt{
    justify-content: center;
}
.front-heading h1{
    text-align: center;
    font-size: 48px;
    font-family: lora,serif;
    font-weight: 600;
    color: rgb(251, 227, 117);
}

@media only screen and (max-width: 576px) {
    .front-heading h1{
        font-size: 30px;
    }
}