}
.accordiantxt{
    color: rgb(159, 234, 164);
    padding: 15px;

}
.accordiantxtp{
    color: white;
}
#panel4bh-header{
    background-color: #202624;
    
    
}
svg{
    color: rgb(159, 234, 164);
}
.MuiAccordionDetails-root{
    background-color: #202624 !important;
}

