@media only screen and (max-width: 576px) {
    .questions h1{
        font-size: 30px;
    }
    .questions p{
        font-size: 16px;
    }
    .accordiantxt{
        font-size: 16px;
    }
    .questions{
        text-align: center;
    }
    .questions p{
        width: 100%;
        margin: auto;
    }
}
@media only screen and (min-width: 576px) and (max-width: 768px) {
    .questions{
        text-align: center;
    }
    .questions p{
        width: 100%;
        margin: auto;
    }
}